
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet
},
name: 'header',
data () {
return {
propUser: this.user,
header: {
leftOptions: {
showBack: false
},
rightOptions: {
showMore: true
},
showMenus: false
}
}
},
created () {



},
methods: {
headerRightClick () {
this.header.showMenus = true
},




menuLogoutClick () {
console.log(JSON.stringify(this.$router))

}
},
computed: {
arragneMenus () {
if (this.propUser.isLogin) {
return {
userName: 'UserName: ' + this.propUser.userName,
name: 'Name: ' + this.propUser.name,
email: 'Email: ' + this.propUser.email,
mobile: 'Mobile: ' + this.propUser.mobile,
logout: '退出登录/Logout'
}







} else {
return null
}
}
},
props: ['user']
}



