
import Search from 'vux/src/components/search/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
directives: { },
components: {
Search,
Datetime,
Group
},
name: 'purchase-order-list',
data () {
return {
propUser: this.user,
propOperation: this.operations,
dateBegin: null,
dateEnd: null
}
},
created () {
this.$http.post(this.propOperation.purchaseOrder.getAllUrl, this.propUser).then(orderRes => {
console.log('order', orderRes)
console.log(JSON.stringify(orderRes.data))
})
},
ready () {
},
methods: {
searchTextChange (val) {
console.log('on-change', val)
}
},
computed: { },
props: ['user', 'operations']
}



